.image{
    height:auto;
    width:10cm;
    cursor: pointer;
}

.arrow{
    width:1cm;
    height: 1cm;
}

.click{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.heading{
    font-size: 30px;
    font-weight: 900;
}

.container{
    border:thin solid grey;
    width:fit-content;
    padding:1cm;
    border-radius: 12px;
    box-shadow: 2px 2px black;
}

.Home{
    padding:1cm;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}